.fof-container{
    display: table;
    width: 100%;
    height: 60vh;
    text-align: center;
    color: #1A202C;
}

.fof{
	  display: table-cell;
	  vertical-align: middle;
}

.fof h1{
    color:#1A202C;
    font-size: 50px;
    display: inline-block;
    padding-right: 12px;
    animation: type .6s alternate infinite;
}

@keyframes type{
	  from{box-shadow: inset -3px 0px 0px #888;}
	  to{box-shadow: inset -3px 0px 0px transparent;}
}